                                    let temp2 = selectObj.anchorNode.parentElement.parentElement.innerHTML;
                                    temp2 = temp2.replace('<span style="font-style : italic">', '');
                                    temp2 = temp2.replace('</span>', '');
                                    selectObj.anchorNode.parentElement.parentElement.innerHTML = temp2;
                                    return;
