/*
여러 줄 선택 시
anchorNode, baseNode는 마지막 노드
focusNode, extendNode는 시작 노드

즉 부모 객체로부터 시작 노드부터 마지막 노드 사이에 글자들을 전부 선택하게 바꾸면 된다.

*/